.wrong__label {
  font-size: 18px;
  text-align: center;
  padding: 40px 0 26px;
  margin: 0;
}

.wrong__description {
  font-size: 14px;
  text-align: center;
  padding: 0 45px 22px;
  margin: 0;
  line-height: 24px;
}

.wrong__button {
  width: 57%;
  padding: 19px 0 15px;
  background-color: #81b3d2;
  border: none;
  display: block;
  margin: 0 auto;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
}

@media (min-width: @tablet-width) {
  .wrong__label {
    padding: 42px 90px 23px;
    font-size: 30px;
    line-height: 40px;
  }

  .wrong__button {
    padding: 20px 0 13px;
    width: 68%;
    margin: 0 auto 49px;
    font-size: 21px;
  }

  .wrong__description {
    padding: 0 45px 31px;
  }
}
