.main-nav {
  position: relative;
  width: 100%;
}

@media (min-width: @tablet-width) {
  .main-nav {
    display: block;
    margin-bottom: 2px;
  }

  .main-nav__close {
    display: none;
  }
}

.main-nav__list {
  background-color: #80b3d1;
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.main-nav__close {
  position: absolute;
  width: 23px;
  height: 22px;
  top: 16px;
  right: 20px;
  cursor: pointer;
  border: none;
  background: transparent;
  fill: #ffffff;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .main-nav__list {
    background-color: #ffffff;
    font-size: 14px;
    display: flex;
    flex-wrap: wrap;
    padding: 0 26px 0 49px;
  }

  .main-nav__item {
    width: 25%;
    margin-top: -2px;
    box-sizing: border-box;
    white-space: nowrap;

    &:nth-child(1) {
      text-align: left;
      margin-right: -39px;
    }

    &:nth-child(2) {
      text-align: left;
      position: relative;
      margin-right: 63px;
    }

    &:nth-child(3) {
      text-align: right;
      margin-right: -26px;
      position: relative;
    }

    &:nth-child(4) {
      text-align: right;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-nav__list {
    padding: 0 69px 0 90px;
  }

  .main-nav__item {

    &:nth-child(1) {
      text-align: left;
      margin-right: -41px;
    }

    &:nth-child(2) {
      text-align: left;
      position: relative;
      margin-right: 63px;
    }

    &:nth-child(3) {
      text-align: right;
      margin-right: -23px;
      position: relative;
    }

    &:nth-child(4) {
      text-align: right;
    }
  }
}

.main-nav__link {
  display: inline-block;
  width: 100%;
  padding: 16px 0 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2);

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
    color: #88b6d1;
  }
}

@media (min-width: @tablet-width) {
  .main-nav__link {
    color: #000000;
    box-shadow: none;

    &:hover {
      color: #669ec0;
      background-color: transparent;
    }

    &:active {
      color: #cacaca;
      background-color: transparent;
    }

    &--active {
      color: #766357;

      &:hover {
        color: #604e43;
      }

      &:active {
        color: #dbd8d5;
      }
    }
  }
}
