.popup__wrong {
  width: 320px;
  min-height: 280px;
  background-color: #ffffff;
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.3);
  position: fixed;
  z-index: 1;
  top: 371px;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  display: none;
}

.popup__success {
  width: 320px;
  background-color: #ffffff;
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.3);
  position: fixed;
  z-index: 1;
  top: 721px;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  display: none;
}

.popup__show {
  display: block;
  animation-name: fadeInDown;
  animation-duration: 0.3s;
}

.popup__hide {
  display: none;
  animation-name: fadeOutUp;
  animation-duration: 0.3s;
}

@media (min-width: @tablet-width) {
  .popup__wrong {
    width: 400px;
    top: 709px;
    max-height: 348px;
  }

  .popup__success {
    top: 541px;
    width: 648px;
    max-height: 348px;
  }
}
