.page-footer {
  width: 100%;
}

@media (min-width: @tablet-width) {
  .page-footer {
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    min-height: 130px;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    width: 80%;
  }
}

.page-footer__label {
  font-size: 21px;
  color: #000000;
  padding: 0;
  margin-bottom: 35px;
  margin-right: 2px;
  text-align: center;
}

.page-footer__social-list {
  max-width: 320px;
  list-style: none;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  margin-right: 3px;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .page-footer__social-list {
    margin: 0 0 5px 4px;
  }

  .page-footer__label {
    margin-left: 2px;
  }
}

.page-footer__link {
  display: inline-block;
  background-color: #80b3d1;
  width: 46px;
  height: 48px;
  position: relative;
  margin: 0;

  & svg {
    fill: #ffffff;
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
  }

  &:active svg {
    fill: #88b6d1;
  }

  &--twitter svg {
    position: absolute;
    top: 17px;
    left: 13px;
  }

  &--facebook svg {
    position: absolute;
    top: 14px;
    left: 18px;
  }

  &--youtube svg {
    position: absolute;
    top: 16px;
    left: 13px;
  }
}

.page-footer__item {
  &:not(:last-child) {
    margin-right: 6px;
  }
}

.page-footer__create-by {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-footer__description {
  font-size: 15px;
  color: #000000;
  padding: 0;
  margin: 0 0 13px 2px;
}

.page-footer__academy-link {
  margin: 0;
  display: block;

  & svg {
    width: 42px;
    height: 42px;
    display: block;
    box-sizing: border-box;
    margin-bottom: 10px;
    margin-right: -5px;
  }
}

.page-footer__academy-link:hover svg {
  fill: #81b3d2;
}

.page-footer__academy-link:active svg {
  fill: #b2b2b2;
}

@media (min-width: @tablet-width) {
  .page-footer__description {
    margin: 0 0 12px 10px;
  }

  .page-footer__academy-logo {
    margin: 0 -3px 6px 3px;
  }

  .page-footer__link {

    &--twitter svg {
      top: 18px;
      left: 14px;
    }

    &--facebook svg {
      top: 15px;
      left: 18px;
    }

    &--youtube svg {
      top: 17px;
      left: 13px;
    }
  }

  .page-footer__academy-link {
    margin: 0 0 0 3px;
  }
}

@media (min-width: @desktop-width) {
  .page-footer__academy-logo {
    margin: 0 -6px 6px 3px;
  }

  .page-footer__label {
    margin-left: 0;
  }

  .page-footer__social-links {
    margin: 0 0 5px 4px;
  }

  .page-footer__academy-link svg {
    margin-right: -7px;
  }
}
