.find-hotel {
  width: auto;
  padding: 0;
  margin: 0;
  text-align: center;
}

.find-hotel__label {
  font-size: 21px;
  line-height: 26px;
  padding: 0;
  margin: 28px 0 19px;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .find-hotel__label {
    font-size: 30px;
    padding: 53px 0 30px;
    margin: 0;
  }
}

.find-hotel__description {
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #333333;
  margin: 0;
  padding: 0 5% 11% 5%;
}

@media (min-width: @tablet-width) {
  .find-hotel__description {
    padding: 0 160px 42px;
  }
}

@media (min-width: @desktop-width) {
  .find-hotel__description {
    padding: 0 380px 46px;
  }
}

.find-hotel__button {
  width: 280px;
  padding: 16px 0;
  background-color: #80b3d1;
  color: #ffffff;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
  margin-bottom: 48px;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
    color: #88b6d1;
  }

  &:disabled {
    background-color: #e5e5e5;
    color: #ffffff;
  }
}

@media (min-width: @tablet-width) {
  .find-hotel__button {
    width: 60%;
    margin: 0 0 58px 0;
  }
}

@media (min-width: @desktop-width) {
  .find-hotel__button {
    width: 38%;
    margin: 0 0 65px 0;
  }
}
