.main-picture {
  width: 100%;
  min-height: 388px;
  position: relative;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-color: #80b3d1;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 27px;
    bottom: -1px;
    left: 0;
    background-image: url("../img/bg-intro-triangle-mobile.svg");
    background-repeat: no-repeat;
  }

  &--small {
    min-height: 157px;
    background-position: -225px 100%;
  }

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-position: -224px 100%;
    background-size: 765px auto;
  }
}

@media (min-width: @tablet-width) {
  .main-picture {
    min-height: 518px;
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: center;

    &::after {
      width: 768px;
      height: 57px;
      bottom: 0;
      left: 0;
      background-image: url("../img/bg-intro-triangle-tablet.svg");
    }

    &--small {
      min-height: 290px;
      background-position: 0 -228px;
    }

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
      background-size: cover;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-picture {
    background-image: url("../img/bg-back-desktop@1x.jpg");

    &::after {
      width: 1200px;
      background-image: url("../img/bg-intro-triangle-desktop.svg");
    }

    &--small {
      min-height: 300px;
      background-position: 0 -217px;
    }
  }

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background-image: url("../img/bg-back-desktop@2x.jpg");
  }
}

.main-picture__slogan {
  width: 280px;
  height: 214px;
  position: absolute;
  top: 90px;
  left: 20px;

  &--small {
    width: 229px;
    height: 40px;
    top: 52px;
    left: 45px;
  }
}

@media (min-width: @tablet-width) {
  .main-picture__slogan {
    width: 460px;
    height: 350px;
    position: absolute;
    top: 77px;
    left: 154px;

    &--small {
      height: 80px;
      top: 95px;
      left: 156px;
    }
  }

  .main-picture__image {
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .main-picture__slogan {
    top: 78px;
    left: 370px;

    &--small {
      top: 102px;
      left: 369px;
    }
  }
}
