.advantages {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 320px;
    height: 37px;
    top: -21px;
    left: 0;
    background-image: url("../img/bg-triangle.svg");
  }
}

@media (min-width: @tablet-width) {
  .advantages::before {
    display: none;
  }
}
