.profits {
  background-color: #80b3d1;
  color: #ffffff;
}

@media (min-width: @tablet-width) {
  .profits {
    display: flex;
    justify-content: space-between;

    &--bridge {
      flex-direction: row-reverse;
    }
  }

  .profits__content {
    width: 33%;
    margin: 0;
  }

  .profits__img {
    width: 67%;
    position: relative;
    background-color: #8F9BFF;
  }
}

.profits__title {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
  padding: 57px 0 0;
  margin: 0 0 5px;

  &--bridge {
    padding: 41px 0 0 0;
    margin: 0 0 5px;
  }
}

@media (min-width: @tablet-width) {
  .profits__title {
    font-size: 21px;
    padding: 45px 20px 8px;
    margin: 0;
  }
}

.profits__number {
  font-size: 14px;
  line-height: 26px;
  color: #ffffff;
  text-align: center;
  margin: 0 0 13px;
}

.profits__text {
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  text-align: center;
  padding: 0 21px 39px;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .profits__number {
    padding: 0 0 18px;
    margin: 0;
  }
}

@media (min-width: @tablet-width) {
  .profits__text {
    padding: 0 43px 43px;
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .profits__title {
    font-size: 21px;
    padding: 52px 94px 8px;
    margin: 0;

    &--bridge {
      padding: 52px 122px 8px;
    }
  }

  .profits__text {
    padding: 0 62px 58px;
    margin: 0;

    &--bridge {
      padding: 0 81px 58px;
    }
  }
}
