.welcome {
  width: 100%;
  min-width: 221px;
}

.welcome__label {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
  color: #000000;
  margin: 42px 0 27px;
  padding: 0 23px 0;
  position: relative;
}

@media (min-width: @tablet-width) {
  .welcome__label {
    padding: 51px 72px 27px;
    font-size: 21px;
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .welcome__label {
    padding: 0 288px 0;
    font-size: 21px;
    margin: 51px 0 27px;
  }
}

.welcome__description {
  font-size: 14px;
  color: #000000;
  margin: 0 0 25px;
  line-height: 26px;
  padding: 0 10px 0;
  position: relative;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .welcome__description {
    padding: 0 140px 0;
    margin: 0 0 50px;
  }
}

@media (min-width: @desktop-width) {
  .welcome__description {
    margin: 0 0 50px;
  }
}
