.feedback {
  margin-bottom: 73px;
}

.feedback__label {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
  color: #000000;
  margin: 40px 0 26px;
  padding: 0;
  position: relative;

  &--introduce {
    margin-top: 19px;
    margin-bottom: 21px;
  }

  &--contacts {
    margin-top: 19px;
    margin-bottom: 20px;
  }

  &--impresion {
    margin-top: 19px;
    margin-bottom: 37px;
  }

  &--places {
    margin-top: 18px;
    margin-bottom: 31px;
  }

  &--emotions {
    margin-top: 18px;
    margin-bottom: 17px;
  }
}

.feedback__description {
  font-size: 14px;
  color: #000000;
  margin: 0;
  line-height: 24px;
  padding: 0 10px 16px;
  position: relative;
  text-align: center;
}

.feedback__section {
  padding: 0 20px;
  border-top: 1px solid #e5e5e5;
}

.feedback__part {
  margin-top: 0;
  margin-bottom: 21px;

  &--middlename {
    margin-bottom: 25px;
  }

  &--number {
    margin-bottom: 13px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 57px;
      height: 57px;
      top: 33px;
      right: 0;
      background-color: #e6e6e6;
      background-image: url("../img/icon-phone.svg");
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
  }

  &--email {
    position: relative;
    margin-bottom: 28px;

    &::after {
      content: "";
      position: absolute;
      width: 57px;
      height: 57px;
      top: 33px;
      right: 0;
      background-color: #e6e6e6;
      background-image: url("../img/icon-mail.svg");
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
  }

  &--radio {
    margin-bottom: 43px;
  }

  &--checkbox {
    margin-bottom: 54px;

    &:last-child {
      margin-bottom: 41px;
    }
  }

  &--text {
    margin-bottom: 51px;
  }
}

.feedback__caption {
  font-size: 14px;
  cursor: pointer;

  &--name {
    padding-right: 9px;
  }

  &--surname {
    padding-right: 10px;
  }

  &--middlename {
    padding-right: 13px;
  }

  &--number {
    margin-bottom: 7px;
    padding-left: 22px;
    display: block;
  }

  &--email {
    margin-bottom: 7px;
    padding-left: 22px;
    display: block;
  }

  &--rbtn {
    padding-left: 79px;
    font-size: 18px;
    text-transform: none;
    font-weight: bold;
    position: relative;
    display: inline-block;

    &::before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      top: -17px;
      left: 0;
      border: 17px solid #f2f2f2;
      background-color: #f2f2f2;
      border-radius: 100%;
    }
  }

  &--check {
    padding-left: 79px;
    font-size: 18px;
    text-transform: none;
    font-weight: bold;
    position: relative;
    display: inline-block;

    &::before {
      content: "";
      position: absolute;
      width: 55px;
      height: 55px;
      top: -16px;
      left: 0;
      background-color: #f2f2f2;
      border-radius: 3px;
    }
  }
}

.feedback__input {
  background-color: #f2f2f2;
  border: 4px solid #f2f2f2;
  min-height: 49px;
  padding: 0 17px;

  &::placeholder {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    opacity: 1;
  }

  &:hover {
    background-color: #e5e5e5;
    border-color: #e5e5e5;
  }

  &:focus {
    background-color: #ffffff;
    border-color: #e5e5e5;
  }

  &--name {
    width: 187px;
  }

  &--surname {
    width: 149px;
  }

  &--middlename {
    width: 151px;
  }

  &--number {
    width: 182px;
  }

  &--email {
    width: 182px;
  }
}

.feedback__text {
  width: 100%;
  max-height: 160px;
  resize: none;
  border: none;
  background-color: #f2f2f2;
  padding: 16px 20px 0;
  display: block;
  box-sizing: border-box;

  &::placeholder {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    opacity: 1;
  }
}

.feedback__wrapper {
  &--submit {
    & p {
      display: none;
    }
  }
}

.feedback__submit {
  font-size: 21px;
  font-weight: bold;
  background-color: #81b3d2;
  color: #ffffff;
  border: none;
  text-transform: uppercase;
  width: 100%;
  padding: 16px 0;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
    color: #88b6d1;
  }
}

.feedback__rbtn:hover + .feedback__caption--rbtn::before {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
}

.feedback__rbtn:checked + .feedback__caption--rbtn::before {
  background-color: #000000;
}

.feedback__rbtn:disabled + .feedback__caption--rbtn::before {
  border-color: #d5d5d5;
  background-color: #d5d5d5;
}

.feedback__rbtn:focus + .feedback__caption--rbtn::before {
  outline: 1px auto #3b99fc;
}

.feedback__check:hover + .feedback__caption--check::before {
  background-color: #e5e5e5;
}

.feedback__check:checked + .feedback__caption--check::before {
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.feedback__check:focus + .feedback__caption--check::before {
  outline: 1px auto #3b99fc;
}

.feedback__check:disabled + .feedback__caption--check::before {
  background-color: #d5d5d5;
}

@media (min-width: @tablet-width) {
  .feedback {
    margin-bottom: 61px;
  }

  .feedback__form {
    display: flex;
    flex-wrap: wrap;
    width: 648px;
    margin: 0 auto;
  }

  .feedback__label {
    font-size: 30px;
    margin-top: 76px;
    margin-bottom: 30px;

    &--impresion {
      text-align: left;
      margin-bottom: 74px;
    }

    &--introduce {
      margin-bottom: 58px;
      margin-left: -40px;
    }

    &--contacts {
      margin-top: 36px;
      margin-bottom: 25px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        top: 50%;
        left: 0;
        background-color: #e6e6e6;
      }

      & span {
        padding: 0 60px;
        background-color: #ffffff;
        position: relative;
        z-index: 1;
      }
    }

    &--places {
      margin-top: 25px;
      margin-bottom: 56px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        top: 50%;
        left: 0;
        background-color: #e6e6e6;
      }

      & span {
        width: 266px;
        display: block;
        margin: 0 auto;
        padding: 0 67px;
        background-color: #ffffff;
        position: relative;
        z-index: 1;
      }
    }

    &--emotions {
      margin-top: 22px;
      margin-bottom: 57px;
    }
  }

  .feedback__description {
    padding: 0 162px;
    margin-bottom: -3px;
  }

  .feedback__section {
    border: none;
    padding: 0;

    & h3 {
      font-size: 21px;
    }

    &--introduce {
      order: 2;
      width: 50%;

      & p {
        margin-left: 58px;
      }
    }

    &--contacts {
      order: 3;
      width: 100%;
    }

    &--impressions {
      order: 1;
      width: 50%;
    }

    &--places {
      order: 4;
      width: 100%;

      & div {
        display: flex;
        flex-wrap: wrap;
        padding: 0 96px 0;
        justify-content: space-between;
      }
    }

    &--emotions {
      order: 5;
    }
  }

  .feedback__part {
    &--radio {
      margin-bottom: 51px;
    }

    &--number {
      margin-bottom: 24px;
    }

    &--checkbox {
      width: 50%;
      margin-bottom: 62px;
      margin-right: -30px;
    }

    &--text {
      margin-bottom: 58px;
    }
  }

  .feedback__caption {
    &--rbtn {
      padding-left: 75px;
    }
  }

  .feedback__input {
    &--name {
      width: 173px;
    }

    &--surname {
      width: 135px;
    }

    &--middlename {
      width: 137px;
    }

    &--number {
      width: 551px;
    }

    &--email {
      width: 550px;
    }
  }

  .feedback__wrapper {
    &--submit {
      display: flex;
      justify-content: space-between;
      align-items: baseline;

      & p {
        display: block;
        font-size: 14px;
      }
    }
  }

  .feedback__submit {
    width: 50%;
  }
}

@media (min-width: @desktop-width) {
  .feedback {
    margin-bottom: 67px;
  }

  .feedback__form {
    width: 960px;
  }

  .feedback__description {
    padding: 0 378px 5px;
  }

  .feedback__label {
    margin-top: 112px;
    margin-bottom: 31px;

    &--impresion {
      margin-top: 57px;
      margin-bottom: 74px;
      margin-left: -45px;
      text-align: center;
    }

    &--introduce {
      margin-top: 57px;
      margin-bottom: 57px;
      margin-left: -162px;
      text-align: center;
    }

    &--contacts {
      margin-top: 34px;
      margin-bottom: 74px;
    }

    &--places {
      margin-top: 25px;
      margin-bottom: 56px;
    }

    &--emotions {
      margin-top: 20px;
      margin-bottom: 57px;
    }
  }

  .feedback__section {
    &--impressions {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 292px;
        top: 62px;
        right: 0;
        background-color: #dbdbdb;
      }
    }

    &--introduce p {
      margin-left: 81px;
    }

    &--places div {
      flex-wrap: wrap;
      justify-content: flex-start;
      padding: 0;
    }

    &--emotions {
      width: 100%;
    }
  }

  .feedback__wrapper {
    &--contacts {
      display: flex;
    }
  }

  .feedback__input {
    &--name {
      width: 306px;
    }

    &--surname {
      width: 268px;
    }

    &--middlename {
      width: 268px;
    }

    &--email {
      width: 217px;
    }

    &--number {
      width: 217px;
    }
  }

  .feedback__part {
    &--number {
      display: flex;
      width: 50%;
      align-items: baseline;

      & span {
        display: none;
      }

      &::after {
        top: 0;
        right: 79px;
      }
    }

    &--checkbox {
      width: auto;
      margin-right: 0;

      &:nth-child(1) {
        margin-left: 0;
        margin-right: 73px;
      }

      &:nth-child(2) {
        margin-left: 0;
        margin-right: 71px;
      }

      &:nth-child(3) {
        margin-left: 0;
        margin-right: 66px;
      }

      &:nth-child(4) {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &--email {
      display: flex;
      width: 50%;
      align-items: baseline;

      &::after {
        top: 0;
      }
    }
  }

  .feedback__caption {
    &--number {
      padding-left: 0;
      width: 86px;
    }

    &--email {
      padding-left: 0;
      width: 165px;
    }

    &--rbtn {
      padding-left: 80px;

      &::before {
        left: 4px;
      }
    }
  }

  .feedback__caption--check::before {
    top: -12px;
    left: -1px;
  }
}
