body {
  min-width: 320px;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  color: #000000;
  background-color: #f2f2f2;
  font-style: normal;
  margin: 0;
  padding: 0;
}

.content-img {
  object-fit: cover;
  width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-js {
  display: none;
}

.hyphenation {
  display: none;
}

@media (min-width: @tablet-width) {
  .container {
    width: 768px;
  }

  .hyphenation {
    display: block;
  }
}

@media (min-width: @desktop-width) {
  .container {
    width: 1200px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  }

  .content-img {
    height: 100%;
    position: absolute;
  }
}
