.video-gallery {
  border-top: 1px solid #e5e5e5;
  margin-bottom: 75px;
}

.video-gallery__description {
  text-align: center;
  line-height: 24px;
  font-size: 14px;
  padding: 0 22px 34px;
  margin: 0;
}

.video-gallery__label {
  font-size: 18px;
  margin: 0;
  padding: 35px 0 26px;
  text-align: center;
}

.video-gallery__wrapper {
  width: 100%;
  min-height: 268px;
  margin: 0 auto;
  position: relative;
}

.video-gallery__controls {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.video-gallery__scrollbar {
  width: 87%;
  margin: 0 auto;
  min-height: 45px;
  background-color: rgba(0, 0, 0, 0.4);
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 235px;
    height: 3px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.3);
  }

  &::after {
    position: absolute;
    content: "";
    width: 9px;
    height: 9px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    border-radius: 100%;
  }
}

.video-gallery__functions {
  width: 87%;
  min-height: 67px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f2f2f2;
  position: relative;

  & svg {
    fill: #5c6870;
  }
}

.video-gallery__fullscreen,
.video-gallery__replay,
.video-gallery__subtitles {
  background-color: transparent;
  border: none;
  position: absolute;
}

.video-gallery__replay {
  top: 22px;
  left: 24px;
  width: 16px;
  height: 20px;
  padding: 0;

  &:hover svg {
    fill: #333333;
  }

  &:active svg {
    fill: #b8b8b8;
  }
}

.video-gallery__subtitles {
  top: 23px;
  left: 173px;
  width: 32px;
  height: 20px;
  padding: 0;

  &:hover svg {
    fill: #333333;
  }

  &:active svg {
    fill: #b8b8b8;
  }
}

.video-gallery__fullscreen {
  top: 23px;
  left: 236px;
  width: 20px;
  height: 20px;
  padding: 0;

  &:hover svg {
    fill: #333333;
  }

  &:active svg {
    fill: #b8b8b8;
  }
}

@media (min-width: @tablet-width) {
  .video-gallery__wrapper {
    width: 648px;
    max-height: 500px;

    & img {
      width: 100%;
      height: fit-content;
    }
  }

  .video-gallery__label {
    font-size: 30px;
    margin-top: 34px;
    padding-bottom: 30px;
  }

  .video-gallery__description {
    padding: 0 162px;
    margin-bottom: 72px;
  }

  .video-gallery {
    border: none;
    min-height: 670px;
    margin-bottom: 42px;
  }

  .video-gallery__controls {
    width: 100%;
    left: 0;
  }

  .video-gallery__scrollbar {
    width: 100%;
    min-height: 93px;

    &::before {
      width: 390px;
      left: 287px;
    }

    &::after {
      left: 210px;
    }
  }

  .video-gallery__functions {
    width: 100%;
    min-height: 93px;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: transparent;
  }

  .video-gallery__replay {
    top: 35px;
    left: 35px;

    & svg {
      fill: #ffffff;
    }

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }

  .video-gallery__subtitles {
    top: 36px;
    left: 524px;

    & svg {
      fill: #ffffff;
    }

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }

  .video-gallery__fullscreen {
    top: 36px;
    left: 587px;

    & svg {
      fill: #ffffff;
    }

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }
}

@media (min-width: @desktop-width) {
  .video-gallery__wrapper {
    width: 800px;
    margin: 0 auto;
  }

  .video-gallery__label {
    padding-bottom: 30px;
    padding-top: 39px;
  }

  .video-gallery__description {
    padding: 0 380px;
    margin-bottom: 70px;
  }

  .video-gallery {
    margin-bottom: 86px;
  }

  .video-gallery__replay {
    top: 35px;
    left: 34px;
    cursor: pointer;

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }

  .video-gallery__subtitles {
    left: 674px;
    cursor: pointer;

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }

  .video-gallery__fullscreen {
    left: 737px;
    cursor: pointer;

    &:hover svg {
      fill: #c2c2c2;
    }

    &:active svg {
      fill: #949494;
    }
  }

  .video-gallery__scrollbar {
    &::before {
      width: 541px;
      left: 362px;
    }
  }
}
