.map {
  height: 350px;
  width: 100%;
  position: relative;
  margin-bottom: 38px;
  background-image: url("../img/bg-map-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-color: #8F9BFF;

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background-image: url("../img/bg-map-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &::after {
    position: absolute;
    content: "";
    width: 27px;
    height: 27px;
    top: 161px;
    left: 146px;
    background-image: url("../img/icon-map-marker.svg");
    display: none;
  }
}

.map__frame {
  width: 100%;
  height: 350px;
}

@media (min-width: @tablet-width) {
  .map {
    margin-bottom: 0;
    background-image: url("../img/bg-map-tablet@1x.jpg");


    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background-image: url("../img/bg-map-tablet@2x.jpg");
      background-repeat: no-repeat;
      background-size: contain;
    }

    &::after {
      left: 370px;
    }
  }
}

@media (min-width: @desktop-width) {
  .map {
    margin-bottom: 3px;
    background-image: url("../img/bg-map-desktop@1x.jpg");

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background-image: url("../img/bg-map-desktop@2x.jpg");
      background-repeat: no-repeat;
      background-size: contain;
    }

    &::after {
      left: 585px;
    }
  }
}
