.success__label {
  font-size: 18px;
  text-align: center;
  padding: 39px 0 16px 0;
  margin: 0;
}

.success__description {
  font-size: 14px;
  text-align: center;
  padding: 10px 49px 33px;
  margin: 0;
  line-height: 24px;
}

.success__wrapper {
  width: 100%;
  background-color: #f2f2f2;
  padding: 20px 0;
}

.success__button {
  width: 88%;
  padding: 18px 0 14px;
  background-color: #81b3d2;
  border: none;
  display: block;
  margin: 0 auto;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
}

@media (min-width: @tablet-width) {
  .success__label {
    font-size: 30px;
    padding-top: 62px;
    padding-bottom: 30px;
  }

  .success__description {
    padding: 0 112px 35px;
  }

  .success__button {
    width: 71%;
    font-size: 21px;
  }

  .success__wrapper {
    width: 100%;
    max-height: 348px;
    background-color: #f2f2f2;
    padding: 42px 0;
  }
}
